const ArticleHeader = props => (
  <div>
    <style jsx>{`
      a {
        float: left;
        margin-right: 7px;
      }
    `}</style>
    <h1>{props.title}</h1>
    <div>
      By: {props.detail.author} / {props.detail.company} |{" "}
      {props.detail.datetime}
    </div>
    <div>
      <span style={{ display: "inline" }}>
        {"Tags:"}
        {props.tag.map((item, key) => (
          <a href={item.url}>{item.label}</a>
        ))}
      </span>
    </div>
  </div>
);
export default ArticleHeader;
